/* buttons */
.btn-custom {
    font-size: 0.7rem;
    letter-spacing: .1rem;
    text-transform: uppercase;
    font-weight: bold;
    color: white;
    padding-top: 1rem;
    padding-bottom: 1rem;
}
.btn-small {
    padding-top: .5rem;
    padding-bottom: .5rem;
}
.btn-outline {
    border-color: white;
}
.btn-primary {
    background: #9b4dca !important;
    border: none !important
}
.btn-link {
    padding: 1rem 3rem;
    background: none;
    border: 1px solid white;
    transition: 0.5s;
}

.btn-main {
    border: none;
    background-size: 150% auto;
    background-image: linear-gradient(to right, #9b4dca 0%, rgb(78, 69, 197) 51%, rgb(41, 125, 194) 100%);
}

.btn-main:hover {
    background-size: 200% auto;
    background-image: linear-gradient(to right, #c334d6 0%, #9b4dca 20%, rgb(78, 69, 197) 50%);
}

.btn-social {
    line-height: 3.3em ;
}

.btn-tg:hover {
    background: rgba(30, 75, 117, 0.438);
}

.btn-ws:hover {
    background: rgba(38, 138, 91, 0.281) ;
}

.btn-yl:hover {
    background: rgba(138, 127, 38, 0.28);
}
.btn-rd:hover {
    background: rgba(138, 47, 38, 0.28);
}

.btn-icon {
    height: 1em;
    width: auto;
    padding-right: 1em;
}

.btn-touch {
    height: 50px;
    display: inline-block;
    margin: 8px;
}

.link {
    color: white
}

.link:hover {
    text-decoration: none;
    color: white;
}

.social-logo {
    height: 50px;
    width: auto;
}
